import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { connect } from 'react-redux';


import axios from 'axios';

class Form extends React.Component {
  
  
  render() {

  

  const { handleSubmit, pristine, submitting } = this.props;

  return (

    <div className="content">
    
      <form onSubmit={handleSubmit} className="form-container card">
        <h2>Sign in to your workspace</h2>
        <div className="form-content">

          <p>Enter your workspace’s <b>Slack URL</b></p>
          <p className="input-area">
            <Field
                name="url"
                component="input"
                type="text"
                placeholder="your-workspace-url"
            />  
            <span> .slack.com</span>
          </p>
          <div>
            <button className="btn btn_large small_top_margin full_width ladda-button" type="submit" disabled={pristine || submitting}>Continue</button>
          </div>
          <p>Don’t know your workspace URL? <a href="/"> Find your workspace </a></p>
        </div>
          
      </form>
      <p  className="text-center">Need to get your group started on Slack? <a href="/">Download the app</a> to get started.</p>
    </div>
  );
}
};

const submit = (values, dispatch) => {
  axios.post('', values)
  .then((response) => {
    console.log(response.data, "Form data");
  }, (error) => {
    console.log(error);
  });
  console.log(values);
};


const mapStateToProps = (state, props) => {
  return {
    data: state.data.formData,
  }
}

const connectedForm = connect(mapStateToProps)(Form)

export default reduxForm({
  form: 'primary', // a unique identifier for this form
  onSubmit: submit 
})(connectedForm);
